/* Import statements */
import React, { Component } from 'react';
import { Link, Route, Switch } from 'react-router-dom';
import Home from './views/home';
import Contact from './views/contact';
import err from './views/404';
import Sidebar from "react-sidebar";

import './App.css';


/* Category component */
// const Contact = () => (
//   <div>
//     <h2>Contact</h2>
//   </div>
// )


/* App component */
class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: false
    };
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
  }

  onSetSidebarOpen(open) {
    this.setState({ sidebarOpen: open });
    
    document.getElementById("main-content").classList.toggle('moved');
  }


  render() {
    return (
      <div>
        <Sidebar
          sidebar={
            <nav className="navbar navbar-light">
              <ul className="nav navbar-nav">

              {/* Link components are used for linking to other views */}
                <li><Link to="/">Home</Link></li>
                <li><Link to="/contact">Contact</Link></li>
              </ul>
            </nav>
          }
          open={this.state.sidebarOpen}
          onSetOpen={this.onSetSidebarOpen}
          styles={{ sidebar: { background: "#004d7ce0", width: "150px" } }}
        >

        </Sidebar>
          <div class="App-header">

          <div id="main-content">
          <div class="toggle-btn" onClick={() => this.onSetSidebarOpen(true)}>
            {/* Open sidebar */}
            <span></span>
            <span></span>
            <span></span>
          </div>


          {/* Route components are rendered if the path prop matches the current URL */}

          <Switch>

           <Route exact path="/" component={Home}/>
           <Route exact path="/contact" component={Contact}/>
           <Route component={err}/>
           <Route exact path="*" component={err}/>
      </Switch>

      {/* <Switch>
        <Route path="/" exact component={Home}/>
        <Route path="/will-match" component={WillMatch}/>
        <Route component={NoMatch} />
      </Switch> */}

          </div>
          </div>

      </div>
    )
  }
}

export default App;


